<script>
    export let listFav;
    export let listDislike;
    export let tagfav;
    import { createEventDispatcher, getContext } from 'svelte';
    import { listOfTag, listPref } from '../lib/sharedVar.js';
    import favPopup from "./favPopup.svelte";
    const { close } = getContext('simple-modal');
    const dispatch = createEventDispatcher();
    function handleClose() {
        close(Popup);
    }

    let isHovered = false;
    function handleMouseHover() { isHovered = true; }
    function handleMouseOut() { isHovered = false; }
    
    let getStyle = () => { 
        let styleTemp= "display: grid; grid-template-columns: "
        return styleTemp+(false ? "100%" : "50% 50%;")
    }
    function handleKeyPress(mode, index){
        return (event) => {
            if (event.key === 'Enter') {
                if(mode=="fav"){
                    if(index==(listFav.length-1)){
                        listFav.push("")
                        inputFav = ""
                        refreshFav = {}
                    }
                }else{
                    if(index==(listDislike.length-1)){
                        listDislike.push("")
                        refresDislike = {}
                    }
                }
            }
        }
    }
    function selectedFav(index) {
        return () =>{
            checkedList[index+1] != checkedList[index+1]
        }
    }
    var refreshFav = {}
    var refresDislike = {}
    var refreshTag = {}
    var inputFav = ""
    var inputDislike = ""
    var checkedList = {}
    createCheckedList()
    function createCheckedList() {
        for(var i=0; i<listOfTag.length; i++){
            let tagId = String(i+1)
            let foundTag = tagfav.findIndex(item => item == tagId)>=0
            checkedList[tagId] = foundTag
        }
    }
    function toggleCheckbox(index){
        return () => {
            console.log(checkedList[index])
            checkedList[index]=!checkedList[index]
        }
    }
    function handleInputText(mode, index) {
		return (event) => {
            if (mode == "fav"){
                listFav[index] = event.target.value;
            }else{
                listDislike[index] = event.target.value;
            }
        }
	}
    function deleteItem(mode, id){
        return ()=>{
            if (mode == "fav"){
                if(listFav.length>1){
                    listFav.splice(id,1)
                    refreshFav = {}
