                    refresDislike = {}
                    inputDislike = ""
                    console.log(listDislike)
                }
            }
        }
    }
    function submitAction(){
        let listFavTemp = listFav.filter( (item) => item != "")
        let listDislikeTemp = listDislike.filter( (item) => item != "")
        let listTagFav = []
        for(var i=0; i<listOfTag.length; i++){
            let tagId = String(i+1)
            if(checkedList[tagId]){
                listTagFav.push(tagId)
            }
        }
        console.log(listTagFav)

