    <div style="justify-content:center;display:flex">
        <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};" on:click={submitAction}>Submit</div>
    </div>
</div>
