export class NewsData {
    init(){
        id = 0
        link = ""
        category = ""
        source = ""
        title = ""
        content = ""
        detail = ""
        size = {height: 0, width: 0}
        isActive = false
        isOption = false
    }


    constructor(id, link, category, source,  title, content, content_size_x, content_size_y, detail) {
        this.id = id;
        this.link = link
        this.category = category;
        this.source = source;
        this.title = title;
        this.content = content;
        this.size = {
          height : content_size_y,
          width: content_size_x
        }
        this.detail = detail;
        this.isOption = false
    }
}