<script>
    import { url, Interactor, NewsData } from "./lib/index.js";
	import { onDestroy } from "svelte";
	import Modal from 'svelte-simple-modal';
	import FavTab from "./module/FavTab.svelte";
    import Share from "./module/share.svelte";
    import { inview } from 'svelte-inview';
    import { onMount } from 'svelte';
    const interactor = new Interactor()
	
    var listNews = []
    var listNewsRefresh = {}
    let pageNews = 1
    let Account = "Account"
    let isOptionShow = true
    let listLoad = {}
    let refreshList = {}
    var scrollListener = {}
    var showPopup = {}
    var modePopup = ""
    var isLoadNow = true
    var isLoadPref = false
    var localPref = {fav: [], dislikes: [], tagfav: [], recomendedWord: []}
    var isShowPopup = false
    var modeList = ""
    var listShowImage = []
    var dataPopup = {}
    let loadingImage = "https://wallpapercave.com/wp/wp4371959.jpg"
    const options = {};
    var categories = []
    var selectedCategory = ""
    var listRecomendedWord = []
    var searchInput = ""
    
    function fetchNewListData(){
        isLoadNow = true
        interactor.fetchListNews(pageNews, modeList, selectedCategory, searchInput, sessionLogin).then(newsDataList => {
			listNews = listNews.concat(newsDataList)
            
            // listNewsRefresh = {}
            if(newsDataList.length>0){
                isLoadNow = false
            }
        })
    }
    function onClickOption(item){
        return () => {
            for(let i=0;i<listNews.length;i++){
                if(listNews[i].id == item.id){
                    listNews[i].isOption=true
                }else{
                    listNews[i].isOption = false
                }
            }
        }
    }

    let windowWidth = window.innerWidth;
    let windowHeight = window.innerHeight;

    function handleResize() {
        windowWidth = window.innerWidth;
        windowHeight = window.innerHeight;
        // console.log(listShowImage.length)
        for(let i = 0;i<listShowImage.length;i++){
            let item = listShowImage[i].item
            let node = listShowImage[i].node
            let sizeArr = returnImageSize(item.size.width, item.size.height)
            let widthTemp = sizeArr[0]
            let heightTemp = sizeArr[1]
            // console.log(node)
            node.target.width = widthTemp;
            node.target.height = heightTemp;
        }
        // console.log(windowWidth)
        // Perform any other actions or updates here
    }

    onMount(() => {
        window.addEventListener('resize', handleResize);

        return () => {
        window.removeEventListener('resize', handleResize);
        };
    });
    // function categoryHighlight(id, node){
    //     return "background-color: #006944;"
    //     if(id==selectedCategory){
    //     }
    // }
    function onPressShare(item){
        return () => {
            if(sessionLogin!=""){
                // console.log(item)
                dataPopup = {url: item.link}
                modePopup = "share"
                showPopup = {}
            }
        }
    }
    function onPressFav(item){
        return () => {
            if(sessionLogin!=""){
                interactor.saveLinkData(item, sessionLogin).then((response)=>{
                    // console.log(response['message'])
                })
            }
            onClickOption(item)()
        }
    }
    function filterCategory(id){
        return () => {
            selectedCategory = id
            pageNews = 1
            listNews = []
            listNewsRefresh = {}
            scrollListener = {}
            fetchNewListData()
        }
        // alert("hafkal")
    }
    function getImageEror(event){
        if (event.target.src != loadingImage){
            event.target.src = loadingImage
        }
    }
    function handleInputSearch(event){
        searchInput = event.target.value
    }
    function handleKeyPressSearch(event){
        if (event.key === 'Enter') {
            pageNews = 1
            listNews = []
            listNewsRefresh = {}
            scrollListener = {}
            fetchNewListData()
        }
    }
    import { listOfTag, listPref } from './lib/sharedVar.js';
    import FavPopup from "./module/favPopup.svelte";
    import PopupManager from "./module/PopupManager.svelte";
    let markAsReadLink = (link) => {
        return url+"passagenews.php?newsreaded="+btoa(encodeURI(link))+"&session="+sessionLogin
    }
	const unsubscribe = listPref.subscribe((value) => {
        if(isLoadPref){
            console.log("check val")
            console.log(value)
            interactor.savePrefUser(btoa(JSON.stringify(value)), sessionLogin).then(() => {
                pageNews = 1
                listNews = []
                getPreference()
                fetchNewListData()
            })
        }
	}); // logs 'got a subscriber', then '1'
    function returnImageSize(width, height) {
        // event.target.style = ""
        let widthImg =  width
        let heightImg = height
        let heigthImage = (heightImg/widthImg)*(windowWidth*(isWideScreen ? 0.45 : 0.85));
        let widthImage = (widthImg/heightImg)*heigthImage;
        return [widthImage, heigthImage]
        // localHeight[event.target.src] = {height: heigthImage, width: widthImage}
        // event.target.width = widthImage;
        // event.target.height = heigthImage;
    }
    function getColorTabBtn(modeCheck){
        if (modeList==modeCheck) {
            return "background-color: #00000033;"
        }
    }
    let isAtBottom = false;

    function titleClick(){
        
    }
    function checkIfAtBottom() {
        const scrollY = window.scrollY;
        const windowHeight = window.innerHeight;
        const documentHeight = document.documentElement.scrollHeight;
        isAtBottom = scrollY + windowHeight  >= (documentHeight-20);
        scrollListener = {}
        if(isAtBottom){
            if(isLoadNow==false){
                pageNews+=1
                fetchNewListData()
            }
        }
    }
    function insertData(item, node){
        listShowImage.push({url: item.link, item: item, node: node})
    }
    function deleteShowImage(url){
        for(let i = 0;i<listShowImage.length;i++){
            if(listShowImage[i].url==url){
                // console.log("is inde "+listShowImage[i].url)
                listShowImage.splice(i)
            }
        }
    }
    function logoutAction(){
        document.cookie = "sessionLogin=";
        document.cookie = "name=";
        document.cookie = "server=";
        window.location.href = window.location.origin+window.location.pathname;
    }
	function onPressGetList(modeCheck){
        return ()=>{
            pageNews = 1
            listNews = []
            listNewsRefresh = {}
            switch(modeCheck){
                case "readed": modeList = "readed";break
                case "unreaded": modeList = "unreaded";break
                case "fav": modeList = "favorite";break
                default: modeList = "";break
            }
            searchInput = ""
            scrollListener = {}
            fetchNewListData()
        }
    }
    function onPressNext(){
        if(isLoadNow==false){
            pageNews+=1
            fetchNewListData()
        }
    }
	function getSession(){
		const searchParams = new URLSearchParams(window.location.search);
		// Get a specific parameter
		const token = searchParams.get('token');
		const name = searchParams.get('name');
        const server = searchParams.get('server');
        // console.log("test name : "+name)
        Account = name
        if ((token == null) || (token == "") || (token == undefined) && 
            (name == null) || (name == "") || (name == undefined) &&
            (server == null) || (server == "") || (server == undefined)){
			// document.cookie = "sessionLogin=";
        }else{
            // console.log("sessionLogin="+token)
			document.cookie = "sessionLogin="+token;
            document.cookie = "name="+name;
            document.cookie = "server="+server;
			window.location.href = window.location.origin+window.location.pathname;
		}
	}
    const getLoginSession = () => {
		const cookieName = 'sessionLogin';
        const username = 'name';
    	const server = 'server';
        const cookies = document.cookie.split('; ');
        // console.log(cookies)
        let nameValue = ""
		let cookieValue = ""
        let servervalue = ""
		for (const cookie of cookies) {
            // console.log("test cookie 2 : "+cookie)
			let splitCookie = cookie.split('=')
			const [name, value] = [splitCookie[0], splitCookie.slice(1, splitCookie.length).join("=")];
            if (name === cookieName) {
				cookieValue = value;
			}else if (name == username){
                nameValue = value;
                Account = value;
            }else if (name == server){
                servervalue = value;
            }
		}
        if(nameValue==null || nameValue==""){
            nameValue = "Login"
        }
        return [cookieValue, nameValue, servervalue]
	}
    getSession()
    const [sessionLogin, userName, server] = getLoginSession()
    // url = atob(server)
    Account = userName
    if((sessionLogin==null ||sessionLogin=="")==false){
        modeList = "unreaded"
    }
    function getPreference(){
        interactor.getPrefUser(sessionLogin).then( data => {
            if(data["responseCode"] != "200"){
                localPref = {fav: [], dislikes: [], tagfav: [], recomendedWord: []}
            }else if(data["fav"].length==0 && data["dislikes"].length==0 && data["tagfav"].length==0){
                // console.log("hello")
                localPref = {fav: [], dislikes: [], tagfav: [], recomendedWord: []}
                isShowPopup = true
                //Show popup
            }else{
                localPref = data
                listRecomendedWord = data.recomendedWord
                let listCategory = data.tagfav.map((x)=>{return {id: x, title: listOfTag[parseInt(x)-1]}})
                categories = [{id: "", title: "All"}].concat(listCategory)
            }
            isLoadPref = true
        })
    }
    getPreference()
    fetchNewListData()
    let isWideScreen = windowWidth>windowHeight
    function windowStyle(){
        let widthCheck = isWideScreen ? "16% 55% 34%" : "100%"
        return "background-color:#141414;display: grid;grid-template-columns:"+widthCheck
    }
    function TabStyleCheck(){
        let widthTabList = sessionLogin ? "20% 20% 20% 20% 20%" : "20% 20%"
        let widthCheck = isWideScreen==false ? widthTabList : "30px 30px 30px 30px 30px"
        let checkRow = isWideScreen==false ? "columns" : "row"
        let centerText = isWideScreen==false ? "text-align: center;" : ""
        let marginBottom = isWideScreen ? "margin-bottom: 15px;" : ""
        return centerText+marginBottom+"margin-top: 10px;display: grid;grid-template-"+checkRow+":"+widthCheck
    }
    function testVis(test){
        console.log("hello boy")
        console.log(test)
    }
    let isHovered = false;
    function handleMouseHover() { isHovered = true; }
    function handleMouseOut() { isHovered = false; }
    
    var hoverID = null
    function handleMouseHoverId(id) { return ()=>{ hoverID = id;isHovered = true; } }
    function handleMouseOutId(id) { return ()=>{ hoverID = null;isHovered = false } }
    function bodyIsPress(event){
        // console.log(event)
        if(!(event.target.id=="optionBtn" || event.target.id=="optionBtnChild")){
            onClickOption({id: null, isOption: false})()
        }
    }
</script>
<style>
    .container {
		display: flex;
	  	justify-content: flex-end ;
	  	align-items: center;
	}
    .hovering-div {
        position: fixed;
        top: 10px;
        left: 10px;
  }
  .hovering-div-right {
        position: fixed;
        top: 10px;
        right: 20px;
  }
</style>
<svelte:window on:scroll={checkIfAtBottom} />
<div style={windowStyle()} on:click={bodyIsPress}>
{#key windowWidth}
<div>
    
        <div class="hovering-div" style={"background-image: linear-gradient(#127956, #032b1d);padding-left: 10px;border-radius: 10px;padding-right: 10px;width: "+(isWideScreen ? "10%" : "100%")+";"+(isWideScreen==false ? "top: 0px;left: 0px;right: 0px;" : "")}>
            <div style={TabStyleCheck()}>
                <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px; color: white;{isWideScreen ? 'padding-top: 40px;' : ''}" on:click={onPressGetList("all")}>
                    <div style="display: flex; align-items: center;">
                    <img src="/passagemain/wave.png" style="width:30px;height:30px">
                    <div style="width:10px"></div>
                    Passage
                    </div>
                </div>
                <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="{getColorTabBtn("")};cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px;color: white" on:click={onPressGetList("all")}>
                    <div style="display: flex; align-items: center;">
                    <img src="/passagemain/list.png" style="width:30px;height:30px">
                    <div style="width:10px"></div>
                    All
                    </div>
                </div>
                {#if sessionLogin}
                    <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="{getColorTabBtn("favorite")};cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px;color: white" on:click={onPressGetList("fav")}>
                        <div style="display: flex; align-items: center;">
                        <img src="/passagemain/like.png" style="width:30px;height:30px">
                        <div style="width:10px"></div>
                        Favorite
                        </div>
                    </div>
                    <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="{getColorTabBtn("unreaded")};cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px;color: white" on:click={onPressGetList("unreaded")}>
                        <div style="display: flex; align-items: center;">
                        <img src="/passagemain/new.png" style="width:30px;height:30px">
                        <div style="width:10px"></div>
                        Unclicked
                        </div>
                    </div>
                    <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="{getColorTabBtn("readed")};cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px;color: white" on:click={onPressGetList("readed")}>
                        <div style="display: flex; align-items: center;">
                        <img src="/passagemain/click.png" style="width:30px;height:30px">
                        <div style="width:10px"></div>
                        Clicked
                        </div>
                    </div>
                {/if}
            </div>
        </div>
    
</div>
<div>
    {#if isWideScreen==false}
        <div style="height: 50px"></div>
    {/if}
    <div style="height:80px;width:100%;color: white;" class="container">
		<Modal styleWindow={{ width: "50%", backgroundColor: "#333"}} styleCloseButton={{backgroundColor: "#333", color: "red"}}>
			{#key isShowPopup}
                {#if (sessionLogin || isShowPopup)}    
                    <FavTab listTagFav={localPref.tagfav.concat("")} isShowPopup={isShowPopup} listFav={localPref.fav.concat("")} listDislike={localPref.dislikes.concat("")}></FavTab>
                {/if}
            {/key}
            {#key showPopup}
                <PopupManager style="background-color: #333" modePopup={modePopup} data={dataPopup}/>
            {/key}
            
		</Modal>
		<div style="margin:10px"><img id="imagepassage" src="https://github.com/oreki9/passage/blob/main/user.png?raw=true" style="width: 50px;height: 50px;border-radius: 50%;"></div><a style="color: white;padding-right: 20px !important;" href="https://passagemain.xyz/login.php?login=true">{Account}</a>
        {#if sessionLogin}
            <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};padding-right:15px" on:click={logoutAction}>Logout</div>
        {/if}
	</div>
    {#if sessionLogin==false}
        <div style="padding-left:20px;color:#ff7373;width: 85%;padding-bottom: 10px">Passagemain is platform to list news/link that hot that day and filter it according to user prefference, you need to login to set your prefference</div>
    {/if}
    <div style="flex-direction: column;background-color:#141414;">
    {#key listNewsRefresh}
        {#each listNews as item}
            <div style="background-color: #2e4b41;margin: 20px;padding: 15px; border-radius: 10px;border: 1px solid white;">
                {#if item.isOption}
                <div style="position: {item.isOption ? "absolute" : "none"}; z-index: 1;right: 0; right: {isWideScreen ? "34%" : "10%"};">
                    <div style="justify-content: flex-end; width: 100%; margin-top: 10px; padding-right: 11%; padding-left: 11%; background-color: #373a37;border-radius: 10px;">
                        <div style="padding-top: 8px;padding-bottom: 8px;cursor: {isHovered ? 'pointer' : 'auto'};" on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} on:click={onPressShare(item)}>
                            <div style="color: white;padding-left: 5px; padding-top: 5px; padding-bottom: 5px">Share</div>
                        </div>
                        <div style="padding-top: 8px;padding-bottom: 8px;cursor: {isHovered ? 'pointer' : 'auto'};" on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} on:click={onPressFav(item)}>
                            <div style="color: white;padding-left: 5px; padding-top: 5px; padding-bottom: 5px">{modeList == "favorite" ? "delete favorites" : "add favorite"}</div>
                        </div>
                    </div>
                  </div>
                {/if}
                <div style="display: grid;grid-template-columns: 97% 3%;padding-right:20px;padding-top: 15px;">
                    <a style="color:#fff;" href={markAsReadLink(item.link)}>
                        <div style="font-size: 18px;">{item.title ?? ""}</div>
                    </a>
                    <div id="optionBtn" on:click={onClickOption(item)} style="width:20px;height:20px;padding: 8px;display: {(sessionLogin==''||sessionLogin==null) ? 'none' : 'flex'};justify-content: center;border-radius: 10px;">
                        <svg id="optionBtnChild" viewBox="0 0 24 24" aria-hidden="true" ><g><path id="optionBtnChild" d="M3 12c0-1.1.9-2 2-2s2 .9 2 2-.9 2-2 2-2-.9-2-2zm9 2c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm7 0c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2z" style="fill: rgb(255, 255, 255);"></path></g></svg>
                    </div>
                </div>
                <div style="padding-top: 10px; justify-content: center;display: flex;">
                    <img use:inview={options}
                    on:inview_change={(event) => {
                    const { inView} = event.detail;
                    let sizeArr = returnImageSize(item.size.width, item.size.height)
                    let widthTemp = sizeArr[0]
                    let heightTemp = sizeArr[1]
                    // insertData(item, event.target)
                    if(inView){
                        event.target.src = item.content
                    }else{
                        event.target.src = loadingImage
                    }
                    event.target.height = heightTemp
                    event.target.width = widthTemp
                    }} style="border-radius: 13px;" src=loadingImage on:error={getImageEror}/>
                </div>
                <div style="color: white;padding-top: 10px">{item.detail ?? ""}</div>
                <div style="color: white;padding-top: 10px">{item.source ?? ""}</div>
            </div>
        {/each}
    {/key}
    </div>
    <div style="color: white;justify-content: center;display: flex;height: 25px" on:click={onPressNext}>
        Press to Load More
    </div>
    <!-- {#key listNewsRefresh}
    {/key} -->
</div>
{#if isWideScreen}
    <div class="hovering-div-right" style={"width: 20%"+(isWideScreen==false ? "top: 0px;left: 0px;right: 0px;" : "")}>
        <div style="background-color: #465054;margin-bottom: 40px;border-radius: 30px;margin-top: 5px">
            <input value={searchInput} on:input={handleInputSearch} on:keydown={handleKeyPressSearch} placeholder="search..." style="border: 0px solid white;background-color: #465054;margin-left: 20px;margin-top: 5px;margin-bottom: 5px;width: 85%;color: #fff" type="text" />
        </div>
        {#if sessionLogin}
            <div style={"color: #fff;background-color: #465054;padding-left: 20px;padding-right: 20px;padding-top: 10px;padding-bottom: 20px;border-radius: 10px;"}>
                <div style="padding-bottom: 5px"><h2>Filter Category</h2></div>
                {#if categories.length==0}
                    You Dont Have Favorite Categories
                {/if}
                {#each categories as cat}
                    <div on:mouseover={handleMouseHoverId(cat.id)} on:mouseout={handleMouseOutId(cat.id)} style="cursor: {isHovered ? 'pointer' : 'auto'};padding-top: 8px;padding-bottom: 8px;padding-left: 10px; padding-right: 10px;background-color:{cat.id==selectedCategory ? (hoverID==cat.id ? "#006944" : "#118055") : (hoverID==cat.id ? "#000000": "#222222")}" on:click={filterCategory(cat.id)}>{cat.title}</div>
                {/each}
            </div>
        {/if}
        <div style={"margin-top: 30px;color: #fff;background-color: #465054;padding-right: 20px;padding-left: 20px;padding-top: 10px;padding-bottom: 20px;border-radius: 10px;border: 1px solid #fff;"}>
            <h3>Recomended word</h3>
            {#if listRecomendedWord.length==0}
                    Recomended Word still Loading...
                {/if}
            {#each listRecomendedWord as word}
                <div style="padding: 5px;border-top: 0.5px solid #fff;"># {word}</div>
            {/each}
        </div>
        <div style="display: flex;flex-direction: row;flex-wrap: wrap;padding-top: 20px">
            <a href={"https://passagemain.xyz/underconstruct.html"} on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};color: #b2b2b2;padding: 2px;margin-left: 5px;margin-right: 5px;">terms of service</a>
            <a href={"https://passagemain.xyz/underconstruct.html"} on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};color: #b2b2b2;padding: 2px;margin-left: 5px;margin-right: 5px;">privacy policy</a>
            <a href={"https://passagemain.xyz/underconstruct.html"} on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};color: #b2b2b2;padding: 2px;margin-left: 5px;margin-right: 5px;">Cookie Policy</a>
            <a href={"https://passagemain.xyz/underconstruct.html"} on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};color: #b2b2b2;padding: 2px;margin-left: 5px;margin-right: 5px;">More</a>
            <div style="color: #b2b2b2;padding: 2px;margin-left: 5px;margin-right: 5px;">2024 - passagemainone</div>
        </div>
    </div>
{/if}
{/key}
</div>