<script>
    export let listFav;
    export let listDislike;
    export let isShowPopup;
    export let listTagFav;
    import { getContext } from 'svelte';
    import favPopup from "./favPopup.svelte";
    const { open, close } = getContext('simple-modal');
    if(isShowPopup){
      goToFav()
    }
    function goToFav(){
      //Todo: add if the only value is empty string
      let isNotClosable = !(listTagFav.length==1  && listFav.length==1 && listDislike.length==1)
      open(favPopup, {tagfav: JSON.parse(JSON.stringify(listTagFav)), listFav: JSON.parse(JSON.stringify(listFav)), listDislike: JSON.parse(JSON.stringify(listDislike)) }, {closeButton: isNotClosable, closeOnEsc: isNotClosable, closeOnOuterClick: isNotClosable,}, { onClosed: () => {  }})
    }
    let isHovered = false;
    function handleMouseHover() { isHovered = true; }
    function handleMouseOut() { isHovered = false; }
    //defect if not have data fav
</script>
<div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};" on:click={goToFav}>Favorites</div>